$white: #FFFFFF;
$dark-blue: #0D345A;
$light-blue: #A9BFD6;

.home {
    margin-top: 2em;
}

.home__img-wrapper {
	position: relative;
	font-size: 4rem;
	
	.home__wrapper {
		display: flex;
		align-items: center;
		
		.home__sofa-img {
			background-color: yellow;
			height: 524px;
			object-fit: cover;
		}

		.home__text-img-overlay {
			display: flex;
			position: absolute;
			padding-left: 0.7em;
		}
	}
}

.gallery__wrapper {
	display: flex;
	flex-direction: column;
	position: relative;
	background-color: $dark-blue;
	color: $white;
	margin-top: 8em;
        
        .gallery__bestsellers-wrapper{
            display: flex;
            margin-bottom: 8em;
            position: relative;
            margin-bottom: 14%;
            
            .gallery__bestsellers {
			width: 85%;
			position: absolute;
			top: -5.6em;
			left: 7.5%;
            
            a {
                text-decoration: none;
                color: $white;
            }

		}
	}

    .gallery__recommended {
        width: 85%;
        margin: 0 auto;
        margin-top: 3em;

        a {
            text-decoration: none;
            color: $white;
        }
    }

    .gallery__outro {
        display: flex;
        flex-direction: column;
    
        h2 {
            font-size: 4.6rem;
            text-align: center;
        }
    
        button {
            margin-top: 3em;
            margin-bottom: 3.5em;
            padding: 0.8em 2em;
            border: 3px solid $white;
            background-color: $dark-blue;
            transition: all 250ms ease-in-out;
    
            a {
                color: $white;
                text-decoration: none;
                font-size: 1.1rem;
            }
        }
    
        button:hover {
            background-color: $white;

            a {
                color: $dark-blue;
            }
        }
    }
}

.book-launch {
	margin-top: 3em;
    margin-bottom: 5em;

    .book-launch__title {
        margin-bottom: 3em;
    }

    .book-launch-wrapper {
        display: flex;
        justify-content: center;
        gap: 5em;
    
        .book-launch-info {
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: 3em;
        }
    
        .book-launch-img {
            max-width: 50%;
            border: 15px solid $dark-blue;
        }
    }
}



/* MEDIA QUERY */
@media (max-width: 675px) {
    .home__img-wrapper{
        font-size: 1.6rem;

        .sofa-img{
            height: 350px;
            object-fit: cover;
        }
    }

    .gallery__title {
        h2 {
            font-size: 2.2rem;
        }
    }

    .home__bestsellers-title-wrapper {
        margin-top: 3em;
    }

    .gallery__wrapper {
        .gallery__bestsellers-wrapper {
            .gallery__bestsellers{
                width: 70%;
                left: 15%;
            }
        }

        .gallery__title.recommended-books {
            margin-top: 16em;
            text-align: center;
        }
        .gallery__recommended{
            width: 70%;
            left: 15%;
        }

        .gallery__outro .gallery__outro-title {
            padding: 0 0.5em;
            font-size: 2.8rem;
        }
    }

    .book-launch {
        .book-launch-wrapper {
            flex-direction: column-reverse;
            align-items: center;

            .book-launch-info {
                gap: 1.3em;
                text-align: center;
            }
        }
    }
}